body {
  font-family: 'Josefin Sans Regular', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  padding-top: 82px;
}
.img-crop {
  position: relative;
  width: 333px;
  height: 500px;
  overflow: hidden;
  img {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    width: auto;
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    &.portrait {
      width: 100%;
      height: auto;
    }
  }
}

